<template>
    <div class="container">
      <el-row>
        <el-col :span="8">
          <h1 class="title">称重</h1>
            
          <label for="">订单号</label><el-input v-model="task.id" placeholder="请输入订单号"></el-input>
          <label for="">长</label><el-input v-model="task.length" placeholder="长"></el-input> 
          <label for="">宽</label><el-input v-model="task.width" placeholder="宽"></el-input> 
          <label for="">高</label><el-input v-model="task.height" placeholder="高"></el-input> 
           
        </el-col>
      </el-row>
      

       <div class="footerButton border-shadow">
        <el-button type="primary" @click="submitTask()" style="vertical-align: top">提交</el-button>
      </div>
    </div>
</template>

<script>
    export default {
      name: "addTask",
      data() {
          return { 
            pageName: '添加',
            task: {
              id: "",
              length: "", 
              width: "", 
              height: "", 
            },
            
           
          }
      },
      methods: {
        submitTask() {
          let url = "http://api.orkowms.me/so-bill/size";
          let params = {};
          params.id = this.task.id;
          params.length = this.task.length; 
          params.width = this.task.width; 
          params.height = this.task.height; 
           
          let that = this;  
          this.$axios.post(url,params).then((res) => { 
             console.log(res.data.message);
             if(res.data.code==500){
              that.$message({
                type: 'error',
                message: res.data.message
              }); 
            }else{
              that.$message({
                type: 'success',
                message: res.data.message
              }); 
            }
              
            
          }).catch((err) => { 
             console.log(err);
              that.$message({
                type: 'error',
                message: '上传失败!'
              }); 
          })
        },
         
      },
      created() {
         
      }
    }
</script>

<style scoped lang="stylus">
  .container
    .el-row
      margin 30px 30px 0px 30px
    .tableContainer
      margin 30px 30px 0px 30px
    .title2
      margin 10px 30px 0px 30px
    label
      display block
      font-weight bold
      font-size: 20px
      margin: 10px 0
    .title
      margin-bottom: 50px
    .button
      margin-top: 20px
    .upload-img
      display inline-block
      margin-right 30px
    .footerButton
      position absolute
      left: 0
      bottom 0
      width: 100%
      padding 13px 30px
      box-sizing border-box
      background-color: hsla(0,0%,100%,.9);
      z-index 2
      button
        float: right;

</style>



